
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

@import "@/assets/variables";

.main {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
}

.heading {
  display: flex;

  &__icon {
    flex: 0 0 130px;
    height: auto;
    margin-right: 30px;

    @include notebook {
      display: none;
    }
  }

  &__title {
    color: var(--text-tertiary);
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 15px;

    @include notebook {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      font-size: 16px;
    }

    &-icon {
      display: none;

      @include notebook {
        display: block;
        width: 24px;
        height: 16px;
        margin-right: 8px;
      }
    }
  }

  &__text {
    line-height: 20px;
  }
}

.form {
  background: var(--bg);
  border: 1px solid #ebebeb;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.08);
  border-radius: 8px;
  padding: 20px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 25px 0 15px;

  @include notebook {
    margin: 10px 0 10px;
    text-align: center;
  }

  &__group {
    margin-top: 10px;
    width: 100%;
    max-width: 410px;
  }
}

.actions {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  text-align: center;

  @include notebook {
    margin-bottom: 10px;
  }

  &__helper {
    background: var(--tertiary-accent);
    border-radius: 2px;
    padding: 4px;
  }

  &__link {
    background: var(--bg-warning);
    border-radius: 2px;
    padding: 4px;
    color: #3d464d;
    font-weight: 400;
    flex-grow: 1;
    text-align: center;
    margin-top: 5px;

    &:before {
      content: "";
      display: none;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 5px 0 5px 5px;
      border-color: transparent transparent transparent $text-secondary;
      margin-right: 6px;
    }
  }
}

.video {
  padding-top: 20px;
  border-top: 1px solid #e5e5e5;

  @include notebook {
    padding-top: 10px;
  }

  &__heading {
    color: var(--text-tertiary);
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 15px;

    @include notebook {
      font-size: 14px;
      margin-bottom: 10px;
    }
  }

  &__wrapper {
    padding-bottom: 56.25%;
    position: relative;
  }

  &__iframe {
    display: block;
    border: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}
